import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Checkbox, Col, DatePicker } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const { RangePicker } = DatePicker;

class ProjectCreateForm extends Component {
  state = {
    check_code:false
  }
  
  handleChangeCode = () => {
    const {setFieldsValue} = this.props.form
    this.setState({check_code:!this.state.check_code})
    if(this.state.check_code===false){
      setFieldsValue({
        ["project_code"]: null,
      })
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("やった")
        values.start_date = values.date[0].format("YYYY-MM-DD");
        values.end_date   = values.date[1].format("YYYY-MM-DD");
        delete values['date'];
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { project } = this.props;
    const { loading, loadingSubmit } = project;
    // const data = project.project ? project.project : [];

    // const used = data.is_used;
    const error = project.errorField ? project.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="projects.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <InputGroup compact>
              <Col span={20}>  
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="projects.code" />}
                  validateStatus={
                    error.project_code || getFieldError("project_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.project_code
                      ? error.project_code
                      : getFieldError("project_code")
                  }
                >
                  {getFieldDecorator("project_code", {
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={this.state.check_code ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{  
                      paddingTop:"5px",
                      paddingLeft: "8px",
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                  
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.name" />}
            >
              {getFieldDecorator("project_name", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
                })
                (<Input 
                  //autosize={{ minRows: 2 }} 
                  //disabled={used ? true : false} 
                  />
                )
              }
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.parent" />}
            >
              {getFieldDecorator("id_project_parent", {
                //initialValue: data.id_project_parent,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //onChange={this.handleChangeRole}
                  //disabled={used ? true : false}
                >
                  {this.props.project.project_parents.map((data, key) => (
                    <Option key={key} value={data.id_project}>
                      {data.project_code + " ("+ data.project_name +")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rangeDate" />}
            >
              {getFieldDecorator("date", {
                //initialValue: moment()
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <RangePicker
                  format="DD MMMM YYYY"
                  showTime
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>
          
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.manager" />}
            >
              {getFieldDecorator("id_employee_manager", {
                //initialValue: data.id_employee_manager,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //onChange={this.handleChangeRole}
                  //disabled={used ? true : false}
                >
                  {this.props.project.employees.map((data, key) => (
                    <Option key={key} value={data.id_employee}>
                      {data.employee_code + " (" + data.first_name + (data.last_name ?  " " + data.last_name :"")  + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.customer" />}
            >
              {getFieldDecorator("id_customer", {
                //initialValue: data.id_customer,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //onChange={this.handleChangeRole}
                  //disabled={used ? true : false}
                >
                  {this.props.project.customers.map((data, key) => (
                    <Option key={key} value={data.id_customer}>
                      {data.customer_code +" ("+data.customer_name+")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
                })
                (
                  <TextArea 
                    autosize={{ minRows: 5 }} 
                    //disabled={used ? true : false} 
                    />
                )}
            </Form.Item>

          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedProjectCreateForm = Form.create({
  name: "create_project"
})(ProjectCreateForm);

export default WrappedProjectCreateForm;
