const actions = {
  getProjects: (page, search, filter) => ({
    type: "GET_PROJECTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createProject: () => ({
    type:"CREATE_PROJECT_REQUEST"
  }),
  storeProject: data => ({
    type: "STORE_PROJECT_REQUEST",
    payload: { body: data }
  }),
  editProject: id_project => ({
    type: "EDIT_PROJECT_REQUEST",
    payload: { id_project: id_project }
  }),
  updateProject: (id_project, data) => ({
    type: "UPDATE_PROJECT_REQUEST",
    payload: {
      id_project: id_project,
      body: data
    }
  }),
  updateProjectHistory: (data) => ({
    type: "UPDATE_PROJECT_HISTORY_REQUEST",
    payload: {
      body: data
    }
  }),
  showProject: id_project => ({
    type: "SHOW_PROJECT_REQUEST",
    payload: { id_project: id_project }
  }),
  deactivateProject: id_project => ({
    type: "DEACTIVATE_PROJECT_REQUEST",
    payload: { id_project }
  }),
  activateProject: id_project => ({
    type: "ACTIVATE_PROJECT_REQUEST",
    payload: { id_project }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
